import KubeflowPipelinesSubmitter from "./KubeflowPipelines";
import {
  Accordion,
  AccordionSummary,
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
} from "@material-ui/core";
import { ComponentSpec } from "../componentSpec";
import { useState } from "react";

interface DebugScratchProps {
  componentSpec?: ComponentSpec;
  setComponentSpec?: (componentSpec: ComponentSpec) => void;
}

const DebugScratch = ({
  componentSpec,
  setComponentSpec,
}: DebugScratchProps) => {
  const [isDialogOpen, setIsDialogOpen] = useState(false);


  return (
    <>
      <KubeflowPipelinesSubmitter componentSpec={componentSpec} />
      <Accordion>
        <AccordionSummary>Summary</AccordionSummary>
      </Accordion>
      <button onClick={(e) => {setIsDialogOpen(true)}}>Show dialog</button>
      <Dialog
        //open={open}
        open={isDialogOpen}
        //onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">
          {"Do something?"}
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
              foo bar
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button color="primary" onClick={(e) => setIsDialogOpen(false)}>Disagree</Button>
          <Button color="primary" onClick={(e) => setIsDialogOpen(false)} autoFocus>
            Agree
          </Button>
        </DialogActions>
      </Dialog>
    </>
  );
};

export default DebugScratch;
