import { useState } from "react";

import { ComponentSpec } from "../componentSpec";

interface KubeflowPipelinesSubmitterProps {
  componentSpec?: ComponentSpec;
}

const KubeflowPipelinesSubmitter = ({
  componentSpec,
}: KubeflowPipelinesSubmitterProps) => {
  const [message, setMessage] = useState("");

  //npm install axios
  return (
    <div>
      <button
        onClick={async (e) => {
          // ! Works !
          const response = await fetch(
            "https://addr-dot-us-central2.pipelines.googleusercontent.com/apis/v1beta1/runs",
            {
              headers: new Headers({
                "Authorization": "Bearer "
              }),
            }
          );
          const responseText = await response.text();
          setMessage(responseText);
        }}
      >
        List KFP runs
      </button>
      <button
        onClick={async (e) => {
          // Error: net::ERR_CERT_AUTHORITY_INVALID
          const response = await fetch(
            "https://IP/api/v1/pods/",
            {
              headers: new Headers({
                "Authorization": "Bearer ",
                "Content-Type": "application/json; charset=utf-8"
              }),
            }
          );
          const responseText = await response.text();
          setMessage(responseText);
        }}
      >
        List K8s cluster objects
      </button>
      <span>{message}</span>
    </div>
  );
};

export default KubeflowPipelinesSubmitter;
